#bg{
  /*height and width are important*/
  width:100%;
  height:100vh; 
}

#titleandlogo{
  margin:auto;
  margin-top:25px;
  margin-bottom:75px;
  display:flex;
  width:95%;
  align-items: center;
}

#titleandlogo #title{
  color:white;
  font-size:1rem;
  margin-left:10px;
  letter-spacing: .5rem;
}

#titleandlogo #logo{
  height:40px;
}

#titleandlogo #time{
  font-size:0.8rem;
  margin:auto;
  margin-right:10px;
  color:white;
  /* padding:2px 10px; */
}

#body{
  width:85vw;
  margin:auto;
}


#body #widget-grid{
  display:grid;
  grid-template-columns:1fr 1fr;
  grid-gap:1rem;
}
#welcome{
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin:auto;
    margin-bottom:50px;
    border:2px solid white;
    padding:25px;
    color:white;
    text-align:center;
    font-size:2.8rem;
}
#notes{
    background-color:#658a99;
    padding:10px 15px;
    margin-bottom:50px;
    border-radius:8px;
    color:white;
}

#notes #loading{
    padding:10px;
    display:flex;
    align-items:center;
    justify-content: center;
}

.note{
    background-color:#e6edf0;
    padding:10px;
    margin:10px 5px;
    border-radius:8px;
}

.note-title{
    padding:8px;
    width:100%;
    max-height:4rem;
    font-size:1.2rem;
    box-sizing:border-box;
    outline:none;
    resize: none;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size:1.2rem;
    color:#658a99;
    border:none;
    background-color:#e6edf0;
    font-weight: bold;
}

/* .note .note-title:focus{
    background-color:white;
} */

.note-body{
    display:block;
    margin:auto;
    width:100%;
    padding:8px;
    box-sizing:border-box;
}

.note-textarea{
    padding:8px;
    width:100%;
    background-color:white;
    border:none;
    max-height:7rem;
    box-sizing:border-box;
    outline:none;
    resize: none;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size:1rem;
}

.buttons{
    display:flex;
    justify-content:flex-end;
    box-sizing: border-box;
    margin:auto;
}

.note .buttons .save-button, #create-note-button, .note .buttons .delete-button{
    background-color:#658a99;
    padding:8px;
    color:#f0ebf4;
    cursor:pointer;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    display:flex;
    align-items:center;
    margin-right:8px;
    margin-bottom:5px;
}

.note .buttons .delete-button{
    background:transparent;
    border:2px solid #4e7686;
    color:#4e7686;
}

#notes #create-new-note{
    padding:10px;
    background-color:#4e7686;
    border:2px solid white;
    width:95%;
    margin:10px auto;
    border-radius: 8px;
    cursor:pointer;
    text-align:center;
    color:white;
}

#notes #create-new-note-box{
    background-color:#bed3db;
    padding:10px;
    margin:10px 5px;
    border-radius:8px;
}

#create-new-note-box .note-title{
    background-color:#bed3db;
}

#create-new-note-box .note-title::-webkit-input-placeholder{
    color:white;
}

#create-new-note-box .note-title:-ms-input-placeholder{
    color:white;
}

#create-new-note-box .note-title::-ms-input-placeholder{
    color:white;
}

#create-new-note-box .note-title::placeholder{
    color:white;
}

#quote{
    margin:50px auto;
    width:100%;
    padding:10px;
    box-sizing: border-box;
    text-align:center;
    color:white;
}

#quote #content{
    width:90%;
    line-height:2rem;
    font-style:italic;
    font-size:1.2rem;
    display:block;
    margin:auto;
}

#quote #line{
    width:150px;
    height:1px;
    margin:20px auto;
    background-color:white;
}

#quote #author{
    display:block;
    margin-top:5px;
}
#weather{
    padding:20px;
    background-color:white;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    width:100%;
    box-sizing: border-box;
    color:black;
    border-radius:8px;
}

#weather #temperature{
    font-size:3rem;
}
